<template>
  <div id="app">
    <h1>在线译</h1>
    <h5 class="text-muted"><a v-on:click="transform"><i class="fa fa-refresh"></i>&nbsp;{{ transformBtn }}</a>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;让语言回归于简单</h5>
    <translateForm v-on:formSubmit="translateText" :isTransform="isTransform"></translateForm>
    <div id="container">
      <div id="res" class="well">
        <translateOutput v-text="translatedText"></translateOutput>
      </div>
    </div>
    <div id="footer">
        <span>Developed by Ming /</span>
        <span>email:18533608762@163.com</span>
    </div>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'

export default {
  name: 'App',
  data:function(){
    return{
      translatedText:"",
      transformBtn:"译词",
      isTransform:true
    }
  },
  components: {
    TranslateForm,
    TranslateOutput
  },
  methods:{
    translateText:function(text,language){
      if(text){
            // alert(text);
        this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180424T092532Z.ed97b7a1aeff0103.c7506aa3257fbc266e55a10ce82185d38fe4843a&lang='+language+'&text='+text).then((response)=>{
          // console.log(response.body.text[0]);
          this.translatedText = response.body.text[0];
        })
      }else{
          var arr = ["_(:> 」∠)_ 小主还没有输入内容哦，尝试输入hello~","小M也可以翻译整句话哟，请切换到译句尝试 ( σ'ω')σ","用小M查词，那是相当的快！快来输入吧 (≥◇≤)"]
          var a = Math.floor(Math.random()*3+0);
          this.translatedText = arr[a];
      }
    },
    transform:function(e){
      e.preventDefault();
      this.translatedText = "";
      var s1 = "译句";
      var s2 = "译词";
      if(this.transformBtn == s1){
        this.transformBtn = s2;
        this.isTransform = true;
      }else if(this.transformBtn == s2){
        this.transformBtn = s1;
        this.isTransform = false;
      }else{
        this.transformBtn = "不好意思，蒙圈了@_@";
      }
    }
  }
}
// alert(document.documentElement.clientHeight);//-352px
</script>

<style>
#container {
  display: flex;
  justify-content: center;
}
#res {
  width: 50%;
  min-width: 300px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 50px;
}
#footer{
  position: absolute;
  right: 10px;
  bottom: 5px;
}
span{
  color: #BBBBBB;
  font-family: "lucida grande", "lucida sans unicode", lucida, helvetica, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
}
@media screen and (max-height: 650px) {
  #footer{
    display: none;
  }
}
@media screen and (min-height: 650px) {
  #footer{
    display: block;
  }
}
a:hover{
  cursor: pointer;
  text-decoration:none; 
  color:#333; 
}
</style>



