<template>
  <div id="translateForm">
      <form id="mainform" class="well form-inline" v-on:submit="formSubmit">
        <input v-if="isTransform" id="input" class="form-control" type="text" v-model="textToTranslate" placeholder="请输入需要翻译的词语">
        <div v-else>
          <textarea id="textinput" class="form-control" type="text" v-model="textToTranslatelong" placeholder="请输入需要翻译的内容" rows="5"></textarea>
        </div>
        <select id="language" class="form-control" v-model='language'>
          <option value="en">English</option>
          <option value="zh">Chinese</option>
          <option value="ru">Russian</option>
          <option value="ko">Korean</option>
          <option value="ja">Janpenese</option>
          <option value="de">German</option>
          <option value="fr">French</option>
        </select>
        <input id="submit" class="btn btn-primary btn-sm" type="submit" value="翻译">
      </form>  
  </div>
</template>

<script>

export default {
  name: 'translateForm',
  props:[
    "isTransform"
  ],
  data:function(){
    return{
      textToTranslate:"",
      textToTranslatelong:"",
      language:"en"
    }
  },
  methods:{
    formSubmit:function(e){
      e.preventDefault();
      if(this.isTransform){
        this.$emit("formSubmit",this.textToTranslate,this.language);
        this.textToTranslatelong="";
      }else{
        this.$emit("formSubmit",this.textToTranslatelong,this.language);
        this.textToTranslate="";
      }
      // alert(this.textToTranslate);
    }
  }
}
</script>

<style>
/*@media screen and (max-width: 1023PX) {*/
  #mainform{
    width: 50%;
    min-width: 300px;
  }
/*}*/
/*@media screen and (min-width: 1023PX) {
  #mainform{
    min-width: 66.6%;
  }
}*/
#translateForm{
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}
#textinput,#input,#language,#submit{
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 20px;
}
#textinput{
  resize:none;
  width: 100%;
}
</style>



